<template>
  <main class="performance">
    <div class="cm mcm">
      <section class="performance_list">
        <div class="page_title">
          <h1>Perfor<br>mance</h1>
        </div>
        <div class="yearl">
          <template v-for="(item, i) in sortedProjects">
            <p :key="i" class="year" v-if="item.year">
              <label>{{item.year}}</label>
            </p>
            <div :key="i" class="projecti" v-else>
              <div class="num">
                <label class="minapper">{{0 + item._idx}}</label>
              </div>
              <div class="title">
                <router-link :to="{ name: 'project', params: { id: item.id }}" class="h3">{{item.title}}</router-link>
              </div>
            </div>
          </template>
        </div>
      </section>
    </div>
  </main>
</template>

<script>
  import API from '@/api'
  import { DateTime } from 'luxon'
  import { mapActions, mapGetters } from 'vuex'

  export default {
    name: 'performance',
    computed: {
      projectsCount () {
        let count = 0
        for (let year in this.projects) {
          if (this.projects.hasOwnProperty(year)) {
            count += this.projects[year].length
          }
        }
        return count
      },
      sortedProjects () {
        let arr = []
        let index = this.projectsCount

        function sortProjects (a, b) {
          return (+DateTime.fromISO(a.releaseYear)) - (+DateTime.fromISO(b.releaseYear))
        }

        function pad (n, width, z) {
          z = z || '0'
          n = n + ''
          return n.length >= width ? n : new Array(width - n.length + 1).join(z) + n
        }

        Object.keys(this.projects).sort().reverse().forEach(year => {
          arr.push({ year })
          this.projects[year].slice().sort(sortProjects).forEach(project => {
            arr.push({
              ...project,
              _idx: pad(--index, Math.floor(Math.log10(this.projectsCount)) + 1)
            })
          })
        })
        return arr
      },

      ...mapGetters({
        projects: 'projects'
      })
    },

    methods: {
      ...mapActions({
        load: 'projects'
      })
    },

    beforeRouteEnter (to, from, next) {
      next(vm => {
        vm.load()
      })
    }
  }
</script>

